    export default {
        name: "order-menu",
	    props: {
            item: {
                type: Object
            },
            address: {
            	type: Array
            },
            mobile: {
            	type: String
            },
            hiddenRemark: {
            	type: Boolean,
            	default() {
            		return false
            	}
            },
            status: {
            	type: Number
            },
            num: {
                type: String
            }
	    },
	    data() {
            return {
	    		detail: {},
                isRefund: false,
                noRefund: false,
                changePrice: false,
                cancelOrder: false,
                callPhone: false,
                notRefund: false,
                isReason: false,
                openAddress: false,
                noAddress: false,
                beRemark: false,
                confirmOrder: false,
                refundOnly: false,
                cityRefund: false,
                beToSend: false,
                showRemark: false,
                price: 0,
                express: 0,
                total: 0,
                refund_price: 0,
                addressId: '0',
                remark: '',
                about: '',
	    	}
	    },
	    methods: {
            // 取消
            cancel() {
            	if(this.item.showMobile) {
            		this.item.showMobile = false;
                	this.$emit('change', this.item);
            	}else {
	                this.about = '';
	                this.isRefund = false;
	                this.beToSend = false;
	                this.noRefund = false;
	                this.changePrice = false;
	                this.cancelOrder = false;
	                this.custom = false;
	                this.refundOnly = false;
	                this.cityRefund = false;
	                this.notRefund = false;
	                this.openAddress = false;
	                this.isReason = false;
	                this.confirmOrder = false;
	                this.addressId = 0;
	                this.beRemark = false;
	                this.confirmOrder = false;
	                this.remark = null;
            	}
            },
            // 去售后订单查看
            toRefundOrder(e) {
            	this.$emit('to', e);
            },
	    	// 备注
            makeRemark(data) {
                this.beRemark = true;
                this.detail = data;
                data.seller_remark ? this.remark = data.seller_remark : null;
            },
            // 提交备注
            toRemark() {
                let that = this;
                that.$request({
                    url: that.$api.app_admin.sellerRemark,
                    method: 'post',
                    data: {
                        order_id: that.detail.id,
                        seller_remark: that.remark
                    }
                }).then(response=>{
                    if(response.code === 0) {
                        that.detail.seller_remark =  that.remark;
                        that.cancel();
                		that.$emit('update', that.detail);
                    }else {
                        uni.showToast({
                            title: response.msg,
                            icon: 'none',
                            duration: 1000
                        });
                    }
                });
            },
            // 设置退货地址
            toRefundAdd() {
                this.noAddress = false;
                uni.navigateTo({
                    url: `/pages/address/address?manual_btn_bg=admin&is_hide_default_btn=1&is_refund_address=1`,
                })
            },
            // 改地址
            toChangeAddress(item) {
                this.beToSend = false;
                let order_no = item.order_no ? item.order_no : this.detail.order_no
                uni.navigateTo({
                    url: '/pages/app_admin/change-add/change-add?order_no='+order_no
                })
            },
            // 查看物流
            toExpressInfo(item) {
                if(item.detail.length === 1 && item.detailExpress[0].express_no) {
                    uni.navigateTo({
                        url: '/pages/app_admin/express/express?id='+item.id+'&express='+item.detailExpress[0].express+'&express_no='+item.detailExpress[0].express_no+'&customer_name='+item.detailExpress[0].customer_name
                    });
                }else {
                    uni.navigateTo({
                        url: '/pages/order/express-list/express-list?order_id='+item.id
                    });
                }
            },
            // 拨打电话
            call() {
                uni.makePhoneCall({
                    phoneNumber: this.status == 2 ? this.item.order.mobile : this.item.mobile
                })
                this.$emit('change', '');
            },
            // 确认收货提示
            toConfirm(e) {
                this.detail = e;
                this.confirmOrder = !this.confirmOrder;
            },
            // 确认收货
            beOrderConfirm() {
                let that = this;
                that.$request({
                    url: that.$api.app_admin.confirm,
                    data: {
                        order_id: that.detail.id
                    },
                    method: 'post'
                }).then(response=>{
                    that.$hideLoading();
                    if(response.code === 0) {
                        uni.showToast({
                            title: response.msg,
                            duration: 1000,
                            type: 'success',
                            mask: false
                        });
                        that.cancel();
                        setTimeout(v=>{
                			that.$emit('update', 0);
                        },1000)
                    }else {
                        uni.showToast({
                            title: response.msg,
                            icon: 'none',
                            duration: 1000
                        });
                    }
                }).catch(() => {
                    that.$hideLoading();
                });
            },
            // 同意取消订单
            toAgreeCancel(e) {
                this.detail = e;
                this.isReason = !this.isReason;
                this.noRefund = false;
            },
            // 取消订单提示
            toCancelorder(e) {
                this.detail = e;
                this.cancelOrder = !this.cancelOrder;
            },
            // 取消订单
            cancelSubmit(type) {
                let that = this;
                uni.showLoading({
                    mask: true,
                    title: '加载中...'
                });
                let url = type == 1 ? that.$api.app_admin.cancel : that.$api.app_admin.force_cancel
                that.$request({
                    url: url,
                    data: {
                        status: that.noRefund ? 2 : 1,
                        remark: that.about,
                        order_id: that.detail.id
                    },
                    method: 'post'
                }).then(response => {
                    uni.hideLoading();
                    if (response.code === 0) {
                        uni.showToast({
                            title: that.noRefund ? '拒绝成功':'取消成功',
                            duration: 1000,
                            type: 'success',
                            mask: false
                        });
                        that.cancel();
                        setTimeout(v=>{
                			that.$emit('update', 0);
                        },1000)
                    } else {
                        uni.showToast({
                            title: response.msg,
                            icon: 'none',
                            duration: 1000
                        });
                    }
                }).catch(response => {
                    uni.hideLoading();
                    uni.showToast({
                        title: response,
                        icon: 'none',
                        duration: 1000
                    });
                });
            },
            // 修改价格
            toChange(e) {
                this.detail = e;
                this.changePrice = !this.changePrice;
                this.price = e.total_goods_price;
                this.express = e.express_price;
                this.total = '￥' + e.total_pay_price;
            },
            // 价格输入验证
            priceInput: function(e) {
                if (e.detail.value > -0.01) {
                    this.total = '￥' + (+e.detail.value + +this.express).toFixed(2);
                } else {
                    this.total = '数据有误';
                }
            },
            // 邮费输入验证
            expressInput: function(e) {
                if (e.detail.value > -0.01) {
                    this.total = '￥' + (+e.detail.value + +this.price).toFixed(2);
                } else {
                    this.total = '数据有误';
                }
            },
            // 确认修改价格
            submitChange() {
                let that = this;
                uni.showLoading({
                    mask: true,
                    title: '加载中...'
                });
                if (this.price > -0.01 && this.express > -0.01) {
                    this.$request({
                        url: this.$api.app_admin.update_price,
                        data: {
                            order_id: this.detail.id,
                            total_price: this.price ? this.price : 0,
                            express_price: this.express ? this.express : 0
                        },
                        method: 'post'
                    }).then(response => {
                        uni.hideLoading();
                        if (response.code === 0) {
                            uni.showToast({
                                title: response.msg,
                                duration: 1000,
                                type: 'success',
                                mask: false
                            });
	                        that.changePrice = false;
	                		that.$emit('update', 0);
                        } else {
                            uni.showToast({
                                title: response.msg,
                                icon: 'none',
                                duration: 1000
                            });
                        }
                    }).catch(response => {
                        uni.hideLoading();
                        uni.showToast({
                            title: response,
                            icon: 'none',
                            duration: 1000
                        });
                    });
                } else {
                    if (!that.price || typeof(that.price) != 'number') {
                        uni.showToast({
                            title: '商品总价必须大于等于0',
                            icon: 'none',
                            duration: 1000
                        });
                    } else if (!that.express || typeof(that.express) != 'number') {
                        uni.showToast({
                            title: '运费必须大于等于0',
                            icon: 'none',
                            duration: 1000
                        });
                    }
                }
            },
            // 发货
            toSend(e,is_send) {
                let that = this;
                that.detail = e;
                if (that.detail.status_cn) {
                    uni.navigateTo({
                        url: '/pages/app_admin/send/send?order_refund_id=' + that.detail.id + '&id=' + that.detail.order_id + '&send_type=' + that.detail.send_type + '&is_send=' + is_send
                    })
                } else {
                    if(that.detail.send_type == 1 && !that.detail.address) {
                        this.beToSend = true;
                    }else {
                        uni.navigateTo({
                            url: '/pages/app_admin/send/send?id=' + that.detail.id + '&send_type=' + that.detail.send_type + '&is_send=' + is_send
                        })
                    }
                }
            },
            // 售后操作


            // 选择退款地址
            chooseAddress(e) {
                if (this.addressId == e) {
                    this.addressId = ''
                }else {
                    this.addressId = e
                }
            },
            // 售后处理
            decline(type) {
                let that = this;
                let para = {
                    order_refund_id: that.detail.id,
                    type: that.detail.type,
                    is_agree: type != 0 ? 1 : 0,
                    refund_price: that.detail.refund_price,
                    merchant_remark: that.about
                }
                if (type == 1) {
                    if (that.addressId < 1) {
                        uni.showToast({
                            title: '请选择地址',
                            icon: 'none',
                            duration: 1000
                        });
                        return false;
                    }else {
                        para.address_id = that.addressId
                    }
                }
                if(type == 4) {
                    para.refund = 1
                }
                if (that.cancelRefund) {
                    type = 2;
                    para.is_agree = 2
                }
                uni.showLoading({
                    mask: true,
                    title: '处理中...'
                });
                that.$request({
                    url: that.$api.app_admin.refund_handle,
                    data: para,
                    method: 'post'
                }).then(response => {
                    uni.hideLoading();
                    if (response.code === 0) {
                        uni.showModal({
                            title: '提示',
                            content: response.msg,
                            showCancel: false,
                            success: function (res) {
                                if (res.confirm) {
                                	that.cancel();
			                        setTimeout(v=>{
			                			that.$emit('update', 0);
			                        },1000)
                                }
                            }
                        });
                    } else {
                        uni.showToast({
                            title: response.msg,
                            icon: 'none',
                            duration: 1000
                        });
                    }
                }).catch(response => {
                    uni.hideLoading();
                    uni.showToast({
                        title: response,
                        icon: 'none',
                        duration: 1000
                    });
                });
            },
            // 同意售后
            agree() {
                let that = this;
                uni.showLoading({
                    mask: true,
                    title: '处理中...'
                });
                if (that.detail.refund_price > 0) {
                    that.$request({
                        url: that.$api.app_admin.refund_handle,
                        data: {
                            order_refund_id: that.detail.id,
                            type: that.detail.type,
                            is_agree: 1,
                            refund_price: that.refund_price,
                            merchant_remark: that.about
                        },
                        method: 'post'
                    }).then(response => {
                        uni.hideLoading();
                        if (response.code === 0) {
                            uni.showModal({
                                title: '提示',
                                content: response.msg,
                                showCancel: false,
                                success: function (res) {
                                    if (res.confirm) {
					                    that.cancel();
				                        setTimeout(v=>{
				                			that.$emit('update', 0);
				                        },1000)
                                    }
                                }
                            });
                        } else {
                            uni.showToast({
                                title: response.msg,
                                icon: 'none',
                                duration: 1000
                            });
                        }
                    }).catch(response => {
                        uni.hideLoading();
                        uni.showToast({
                            title: response,
                            icon: 'none',
                            duration: 1000
                        });
                    });
                } else {
                    uni.showToast({
                        title: '退款金额需大于零',
                        icon: 'none',
                        duration: 1000
                    });
                }
            },
            // 拒绝售后
            beNotRefund(e) {
                this.detail = e;
                this.isReason = !this.isReason
                this.noRefund = !this.noRefund;
            },
            // 售后操作
            refundHandle(type,detail,cancel) {
                if (cancel == 1) {
                    this.cancelRefund = true;
                }
                if (type == 1) {
                    if(detail.type == 3) {
                        this.refundOnly = true;
                        this.detail = detail;
                    }else if(detail.order.send_type != 0){
                        this.cityRefund = true;
                        this.detail = detail;
                    }else {
                        if (this.address.length == 0) {
                            this.noAddress = true;
                        } else {
                            this.detail = detail;
                            this.openAddress = true;
                        }
                    }
                } else if (type == 2) {
                    this.detail = detail;
                    this.notRefund = true;
                }
            },
            // 退款提示
            toRefund(e) {
                this.detail = e;
                this.refund_price = e.refund_price;
                this.isRefund = !this.isRefund;
            },
            // 查看图片
            look(e) {
                uni.previewImage({
                    current: e, // 当前显示图片的http链接
                    urls: [e] // 需要预览的图片http链接列表
                })
            },
            // 查看售后理由
            lookAbout(e) {
                this.detail = e;
                this.isReason = true;
            },
            // 售后确认收货
            beConfirm() {
                let that = this;
                that.$request({
                    url: that.$api.app_admin.shou_huo,
                    data: {
                        refund_order_id: that.detail.id
                    },
                    method: 'post'
                }).then(response=>{
                    that.$hideLoading();
                    if(response.code === 0) {
                        that.cancel();
                        that.page = 1;
                        uni.showLoading({
                            mask: true,
                            title: '加载中...'
                        });
                		that.$emit('update', 0);
                    }else {
                        uni.showToast({
                            title: response.msg,
                            icon: 'none',
                            duration: 1000
                        });
                    }
                }).catch(() => {
                    that.$hideLoading();
                });
            },
	    }
    }
